.breed-select {
  border-radius: 8px;
  padding: 3px;
}

.cat-info {
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.cat-img {
  width: 300px;
  border-radius: 8px;
}

.cat-temperament {
  color: #3d3b3b;
  font-weight: 700;
}

h2 {
  color: #3d3b3b;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #cc0a27;
  font-weight: 500;
}

.loader, .error {
  display: none;
}

.loader.active, .error.active {
  display: block;
}

